
import { getBarAllInfo, isSubscribe, getNewestMsg, getMaxMsg } from '@/api/'
import { prefixImageUrl } from '@/utils/utils'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import MarqueeTips from 'vue-marquee-tips'
import BpDialog from '../components/bpDialog'
import InfiniteLoading from 'vue-infinite-loading'
import FooterMain from '../components/Main/Footer'
import Msg from '../components/Main/Msg'
import MsgImg from '../components/Main/MsgImg'
import MsgOnlyImg from '../components/Main/Img'
import BpMsg from '../components/Main/BpMsg'
import DsMsg from '../components/Main/DsMsg'
import BpWindow from '../components/Main/BpWindow'
import DsWindow from '../components/Main/DsWindow'
import Onlines from '../components/Main/Onlines'
import Levels from '@/assets/level/level-show'
import { mapGetters, mapActions } from 'vuex'
import '@/vendor/tween'
import '@/vendor/animation'

export default {
data () {
return {
Levels: Levels,
show: false,
chatlist: [],
list: [],
onlinePeople: [{
sex: 2,
name: '',
avatar: 'https://tvax2.sinaimg.cn/crop.0.0.512.512.50/4e7f0c83ly8fe9xa2lqksj20e80e83z3.jpg'
}, {
sex: 1,
name: '',
avatar: 'https://tva4.sinaimg.cn/crop.0.0.180.180.50/67dd74e0jw1e8qgp5bmzyj2050050aa8.jpg'
}, {
sex: 1,
name: '',
avatar: 'https://tvax3.sinaimg.cn/crop.0.0.512.512.50/8216df68ly8ff1b8rtm8zj20e80e83yn.jpg'
}, {
sex: 1,
name: '',
avatar: 'https://tva1.sinaimg.cn/crop.0.3.1018.1018.50/a716fd45gw1ev7q2k8japj20sg0sg779.jpg'
}, {
sex: 1,
name: '',
avatar: 'https://tva1.sinaimg.cn/crop.0.0.440.440.50/6927e7a5jw8ezf44123d4j20c80c80tg.jpg'
}, {
sex: 1,
name: '',
avatar: 'https://tva4.sinaimg.cn/crop.0.0.512.512.50/b6f28977jw8eqb042gir6j20e80e8aa9.jpg'
}, {
sex: 2,
name: '',
avatar: 'https://tvax4.sinaimg.cn/crop.0.0.750.750.50/a0c1fb53ly8fn3kukoes5j20ku0kudh5.jpg'
}, {
sex: 2,
name: '',
avatar: 'https://tvax1.sinaimg.cn/crop.0.9.494.494.50/bd30a422ly8fj0d8js9zwj20dq0e874q.jpg'
}, {
sex: 2,
name: 'Ariel',
avatar: 'https://tvax1.sinaimg.cn/crop.0.0.512.512.50/6a661712ly8fnshg7z2z7j20e80e8js8.jpg'
}, {
sex: 1,
name: 'jasonchenmusic',
avatar: 'https://tvax4.sinaimg.cn/crop.0.1.509.509.50/73a68773ly8fea294f5i0j20e50e83yq.jpg'
}],
bpWindowVisible: false,
dsWindowVisible: false,
userDialogVisible: false,
shareMaskVisible: false,
buyDialogVisible: false,
concernVisible: false,
onlineVisible: false,
height: 0,
noMore: false,
barDataInfo: {},
adVisible: true,
newsTimer: null,
requestParams: {
ht_id: this.$route.params.id,
min_id: 0
},
requestNewParams: {
ht_id: this.$route.params.id,
id: 0
},
firstLoading: true
}
},
created () {
if (Object.keys(this.userInfo).length === 0) {
this.getUserInfo()
}
getBarAllInfo({ht_id: this.$route.params.id}).then((res) => {

res.result.advert && (res.result.advert.phone.url = prefixImageUrl(res.result.advert.phone.url))
this.barDataInfo = res.result
var img = new Image()
img.onload = () => {
this.show = true
setTimeout(() => {
this.adVisible = false
}, 1000)
}
img.src = res.result.advert.phone.url
})
isSubscribe().then(() => {
}).catch(() => {
this.concernVisible = true
})
this.loopGetNewMsg()
},
mounted () {
/* setTimeout(() => {
var content = document.querySelector('.main-content')
Math.animation(content.scrollTop, content.scrollHeight - content.offsetHeight, function (value) {
content.scrollTop = value
}, 'Linear', 400)
}, 5000) */
},
watch: {
height: function (newVal, oldVal) {
var diff = newVal - oldVal
this.$refs.scrollWrapper.scrollTop = diff
}
},
methods: {
...mapActions('user', [
'getUserInfo'
]),
loopGetNewMsg () {
this.newsTimer = setTimeout(() => {
getMaxMsg(this.requestNewParams).then((res) => {
if (Array.isArray(res.result) && res.result.length > 0) {
this.chatlist = this.chatlist.concat(res.result)
this.$nextTick(() => {
this.scrollToEnd()
})
res.result.sort((a, b) => b.id - a.id)
this.requestNewParams.id = res.result[0].id
}
clearTimeout(this.newsTimer)
this.loopGetNewMsg()
})
}, 5000)
},
scrollToEnd () {
var content = document.querySelector('.main-content')
Math.animation(content.scrollTop, content.scrollHeight - content.offsetHeight, function (value) {
content.scrollTop = value
}, 'Linear', 400)
},
infiniteHandler ($state) {
let params = Object.assign({}, this.requestParams)
getNewestMsg(params).then((res) => {
if (!Array.isArray(res.result)) {
return false
}
var resLength = res.result.length
if (resLength > 0) {
this.requestParams.min_id = res.result[resLength - 1].id
}
res.result.sort((a, b) => a.id - b.id)
res.result.map((v) => {
v.levelIcon = this.Levels[v.grade_title] ? this.Levels[v.grade_title] : null
})
this.chatlist = res.result.concat(this.chatlist)
this.$nextTick(() => {

var chatLength = this.chatlist.length
this.requestNewParams.id = chatLength > 0 ? this.chatlist[chatLength - 1].id : 0
})
this.height = this.$refs.scrollWrapper.scrollHeight
if (res.result.length < 10) {
$state.complete()
} else {
$state.loaded()
}
})
},
showCard () {
this.userDialogVisible = true
},
like (data) {
console.log(data)
data.likes++

},
bp () {
this.onlineVisible = false
this.userDialogVisible = false
this.bpWindowVisible = true
},
ds () {
this.onlineVisible = false
this.userDialogVisible = false
this.dsWindowVisible = true
},
confirmBuy () {
this.buyDialogVisible = false
/* setTimeout(() => {
this.buyDialogVisible = false
}, 3000) */
},
screenForAll () {
this.$store.commit('main/SET_CURRENT_USER_INFO', {})
this.bpWindowVisible = true
},
rewardForAll () {
this.$store.commit('main/SET_CURRENT_USER_INFO', {})
this.dsWindowVisible = true
}
},
computed: {
...mapGetters('app', {
buyDialogInfo: 'buyDialogInfo'
}),
...mapGetters('user', {
userInfo: 'userInfo'
}),
...mapGetters('main', {
currentUserInfo: 'currentUserInfo'
})
},
components: {
MarqueeTips,
FooterMain,
BpDialog,
Msg,
MsgImg,
MsgOnlyImg,
BpMsg,
DsMsg,
BpWindow,
DsWindow,
Onlines,
XDialog,
InfiniteLoading
}
}



