import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
import { mapGetters, mapActions } from 'vuex'
export default {
  model: {
    prop: 'visible',
    event: 'closeWindow'
  },
  data () {
    return {
      dsTimes: 1,
      dsGiftIndex: -1,
      swiperDsGiftOption: {
        slidesPerColumn: 2,
        slidesPerView: 4,
        slidesPerColumnFill: 'row',
        freeMode: true,
        pagination: {
          el: '.swiper-pagination'
        }
      }
    }
  },
  props: ['visible', 'gifts'],
  methods: {
    ...mapActions('app', {
      ChangeBuyDialogInfo: 'ChangeBuyDialogInfo'
    }),
    closeWindow () {
      this.$emit('closeWindow', false)
    },
    buy () {
      if (this.dsGiftIndex === -1) {
        this.$vux.toast.show({
          text: ''
        })
        return false
      }
      this.ChangeBuyDialogInfo({price: Math.floor(Math.random() * 100), rest: 30})
      this.$emit('onBuy')
    },
    reset () {
      this.dsGiftIndex = -1
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  computed: {
    total () {
      const giftPrice = this.dsGiftIndex !== -1 ? Number(this.gifts[this.dsGiftIndex].price) : 0
      return Number(giftPrice * this.dsTimes).toFixed(2)
    },
    timesDsText () {
      const texts = ['', '', '']
      return texts[this.dsTimes - 1] + ''
    },
    ...mapGetters('main', {
      currentUserInfo: 'currentUserInfo'
    })
  }

}



